import React, {Component} from 'react'
import '../style.css'

class Section extends Component {
    constructor(props) {
        super(props)
        this.state = {
            showAdd: false,
            showRemove: false
        }

        this.removeButton = this
            .removeButton
            .bind(this)

        this.pushButton = this
            .pushButton
            .bind(this)
    }

    pushButton = () => {
        const {showAdd} = this.state
        this.setState({
            showAdd: !showAdd
        })

    }

    removeButton = () => {
        const {showRemove} = this.state
        this.setState({
            showRemove: !showRemove
        })
    }

    render() {
        return (
            <div>
                <div>
                    <nav className="grey lighten-2 navbar">
                        <div className='container center'>
                            <ul className="nav">
                                <li>
                                    <a onClick={this.pushButton} className="btn purple darken-3" href="/#">PUSH</a>
                                </li>
                                <li>
                                    <a className="btn purple darken-3" href="/#">UNSHIFT</a>
                                </li>
                            </ul>
                            <ul className="nav">
                                <li>
                                    <a onClick={this.removeButton} className="btn purple darken-3" href="/#">SPLICE</a>
                                </li>
                                <li>
                                    <a className="btn purple darken-3" href="/#">POP</a>
                                </li>
                                <li>
                                    <a className="btn purple darken-3" href="/#">SHIFT</a>
                                </li>
                            </ul>
                            <ul className="nav">
                                <li>
                                    <a className="btn purple darken-3" href="/#">REVERSE</a>
                                </li>
                                <li>
                                    <a className="btn purple darken-3" href="/#">INDEX OF</a>
                                </li>
                            </ul>
                            <ul>
                                <li>
                                    <a className="btn purple darken-3" href="/#">SPLIT</a>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
                <div className="functions pushfunction">
                    {this.state.showAdd && <PushItem/>}
                    {this.state.showRemove && <RemoveItem/>}
                </div>
            </div>
        )
    }
}

class PushItem extends Component {
    render() {
        return (
            <div>
                <div className="center">
                    <p className="center-align infopush">
                        <b>push&nbsp;
                        </b>
                        adds an element to the end of the array,
                        <a
                            href="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/push"
                            target="_blank"
                            rel="noopener noreferrer">&nbsp;read more...</a>
                    </p>
                </div>
                <div className="center-align row">

                    <input type="text" className="pushinput"/>

                    <button className="btn purple darken-3">PUSH</button>

                </div>
            </div>
        )
    }
}

class RemoveItem extends Component {
    render() {
        return (
            <div>
                <div className="center">
                    <p className="center-align infopush">
                        <b>splice&nbsp;</b>
                        removes an element to the end of the array,
                        <a
                            href="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/splice"
                            target="_blank"
                            rel="noopener noreferrer">&nbsp;read more...</a>

                    </p>
                </div>
            </div>
        )
    }
}

export default Section