import React, {Component} from 'react'
import Section from './components/Section'
import Arrays from './components/Arrays'
import logo from './assets/image/javascript-array-visualizer_logo.png'

function Header() {
    return <img src={logo} className="logo" alt="jsarrayvisualizer"/>
}

class App extends Component {
    render() {
        return (
            <div>
                <div className="title">
                    <Header/>
                </div>
                <div>
                    <Section/>
                </div>
                <div>
                    <Arrays />
                </div>
            </div>
        )
    }
}

export default App